$body-color: #fff;
$bg-color:#11131b;
$bg-color-light: #1a1d28;
$color-green: #238636;
$font-color:#6a748e;
$color-red:#e9576f;
#faq-container {
    h2 {
        padding-bottom: 20px;
    }
}
.faq-list {
    padding: 0px 20px;
    list-style: none;
    width: 100%;
    margin: 0px auto;
    max-width: 800px;
}

.faq-item {
    .faq-question {
        border:1px solid $bg-color-light;
        background-color: $bg-color-light;
        padding: 10px;
        cursor: pointer;
        font-weight: bold;
    }
    .faq-question:hover {
        background-color: #1a1d2881;
    }

    .faq-answer {
        transition: ease .4s height;
        padding: 0px 20px;
        margin-top: 10px;
        height: 0px;
        overflow: hidden;;
    }
    &.-active {
        .faq-answer {
            height: 50px;
        }
    }
}