.center {
    text-align: center;
  }
  
  .p-b-10 {
    padding-bottom: 10px;
  }
  .m-b-20 {
    margin-bottom: 20px;
  }
  .m-b-10 {
    margin-bottom: 10px;
  }
  .m-b-5 {
    margin-bottom: 5px;
  }
  .m-b-0 {
      margin-bottom: 0px;
  }

  .display-none {
      display: none;
  }


  .justify-center {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .warning {
    max-width: 500px;
    border:1px solid #e9576f;
    padding: 5px 10px;
    border-radius: 10px;
    color: #e9576f;
    font-size: 11px;
  }


.-seed-notif {
  max-width: 200px;
  text-align: center;
  margin: 20px auto;
  font-weight: bold;
  font-size: 14px;
    padding: 5px 10px;
    border-radius: 10px;
}

.-seed-warning {
  border:1px solid #e9576f;
  color: #e9576f;
}

.-seed-success {
  border:1px solid #238636;
  color: #238636;
}


