$body-color: #fff;
$bg-color:#11131b;
$bg-color-light: #1a1d28;
$color-green: #238636;
$font-color:#6a748e;
$color-red:#e9576f;
body {
  background-color: $bg-color;
  color: $body-color;
  font-family: 'Poppins', sans-serif;
  padding: 0;
  margin: 0;
}

.header {
  // margin-bottom: 20px;
  h1 {
    font-weight: 200;
    margin-bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.logo {
  width: 358px;
}
  p {
    margin-top: 5px;
  }
}

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding:0px;
  margin-bottom: 40px;
}

.box-content {
  background-color: $bg-color-light;
  // min-height: 50vh;
  max-width: 800px;
  width: 100%;
  border-radius: 24px;
  padding: 10px 20px;
  @media only screen and (max-width: 800px) {
    border-radius: 0px;
    padding: 10px 0px;
  }
}
.box-content.-widget {
  max-width: 500px;
  margin-bottom: 10px;
  // min-height: auto;

  .section {
    h2 {
      margin-top: 10px;
    }
  }
}

.nft-list {
  list-style: none;
  width: 100%;
  padding: 0px;
  min-height: 30vh;
}

.nft-item {
  --tw-border-opacity: .1;
  min-height: 70px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, .1);
  padding: 10px 20px;
  border: 1px solid rgba(0, 0, 0, .1);
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  margin-bottom: 5px;
  position: relative;

  .item-close, .btn-close  {
    position: absolute;
    right: -8px;
    top: -8px;
    border-radius: 50%;
    background: $font-color;
    width: 25px;
    color: white;
    
    font-size: 10px;
    height: 25px;
   
    align-items: center;
    justify-content: center;
    cursor: pointer;
    display: none;
  }

}

.nft-item:hover {
  border: 1px solid white;
  .item-close, .btn-close  {  display: flex; }
  
}

.actions {
  display: flex;
  justify-content: center;
  align-items: center;
}
.add-button {
  color:$body-color;
  display:flex;
  justify-content: center;
  align-items: baseline;
  justify-self: center;
  text-decoration: none;
  padding: 5px 20px;
  border-radius: 10px;
  background-color: #000;
  span {
    font-size: 20px;
    margin-right: 5px;
  }
}

.add-button:hover {
  background-color: rgba(0, 0, 0, .7);
}

.form-group {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  margin: 0px 5px;
  label {
    color: $font-color;
    font-size: 10px;
  }
  input {
    border-radius: 5px;
    padding: 5px 10px;
    border:none;
    margin-bottom: 5px;
  }
}

.form {
  padding: 20px;
  padding-bottom: 0px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.stat-group {
  span {
    color: $font-color;
    font-size: 12px;
    text-align: center;
    display: block;
  }
  h2 {
    font-size: 15px;
    font-weight: 200;
  }
  
}

.footer {
  // position: absolute;
    bottom: 0px;
    /* margin: 0 auto; */
    right: 0;
    left: 0;
    text-align: center;
    margin-bottom: 20px;

}
.by {
  color: $font-color;
    font-size: 10px;
    text-decoration: none;
    font-weight: 200;
    
}
.by:hover {
  transition: ease-out all .2s;
  text-decoration: underline;
  font-size: 15px;
}

.wallet {
  font-size: 11px;
  padding:10px;
  border-radius: 10px;
  background-color: #1a1d28;
}

.modal {
  .desc {
    text-align: left;
    font-size: 13px;
    color: $body-color;
  }
}


.batch-table {
  border-collapse: collapse;
  margin: 10px auto;
  td, th {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: center;
  }

  td.active, th.active {
    background-color: $font-color;
    color: $bg-color;
  }

  th {
    background-color: $bg-color-light;
  }
  
}

.btn {
  font-weight: 200;
  
  margin:0px auto;
  width: 100px;
  color:$body-color;
  display:flex;
  justify-content: center;
  align-items: baseline;
  justify-self: center;
  text-decoration: none;
  padding: 5px 20px;
  border-radius: 10px;
  background-color: #000;
  cursor: pointer;
  span {
    font-size: 20px;
    margin-right: 5px;
  }
}

.btn:hover {
  background-color: rgba(0, 0, 0, .8);
}

.donate-section {
  padding-top: 20px;
  margin-bottom: 20px;
  .donate-btn {
    background-color: $color-green;
    color: $body-color;
    margin-bottom: 10px;
  }
  .desc > a {
    color: $font-color;
    font-size: 12px;

  }
}

.batch-table.-mobile {
  display: none;
  .batchbody-mobile {
    margin: 0 auto;
  }
}

.section {
  // margin-bottom: 50px;
  width: 100%;
  // margin-top: 50px;
  .invalid {
    display: none;
    text-align: center;
  }

  h2 {
    text-align: center;
    font-weight: 200;
    margin-bottom: 0px;
  }

  .credits {
    color:$font-color;
    text-align: center;
    font-size: 10px;
    margin-bottom: 0px;
    margin-top: 0px;
  }

  .site {
    color:$font-color;
    text-align: center;
    font-size: 10px;
    margin-top: 0px;
  }
 
}

.section.-date-le-tools {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;

  @media only screen and (max-width: 800px) {
    flex-direction: column;
  }
  .box-content {
    margin: 10px 20px;
  }
}

.next-batch {
  color:$font-color;
  text-align: center;
  font-size: 15px;
  margin-top: 0px;
  margin: 0;
}
.next-batch-timer {
  margin: 0;
}

.seed-compute {
  max-width: 500px;
  margin: 20px auto;
}

.list-container {
  max-width: 500px;
  margin: 20px auto;
}
.weather-effects {
  // padding: 20px 0px;
  max-width: 500px;
  margin: 5px auto 10px;
  .stat-group {
    width: 50%;
  }
  .nft-item {
    padding: 20px 0px;
    justify-content: center;
  }
  .effects {
    margin-bottom: 10px;
    margin: 0px 15px;
    // min-width: 120px;
  }

  .effects > span {
    font-weight: 600;
    
  }
  .stat-group:first-of-type {
    border-right: 1px solid #404758;
  }
  .stat-group {
    
    .negative-effects {
      span {
       color: $color-red;
       text-align: left;
     }
   }
   .positive-effects {
      
      span {
       color: $color-green;
       text-align: right;
     }
   }
  }
  
}
.seed-currency {
  max-width: 500px;
  margin: 5px auto 10px;

  // .nft-item {
  //   .stat-group {
  //     span {
  //       font-size: 10px;
  //     }
      
  //     h2 {
  //       font-size: .7rem;
  //     }
  //   }
  // }
}

.notes {
  color:$font-color;
  text-align: right;
  font-size: 10px;
  padding: 10px;
}
.seed-checker.-hide {
   > .seed-checker-table {
     display: none;
   }
   > .invalid {
     display: block;
   }
   
}

.market-price {
  display: flex;
  justify-content: center;
  margin-bottom: 15px;
    align-items: center;
    flex-direction: row;
    @media only screen and (max-width: 600px) {
      flex-direction: column;
    }

}
.coingecko {
  width: 300px;
  // margin: 20px auto;
  margin: 10px;

    .cg-container {
      padding: 10px;
      border-radius: 10px;
    }
}

.seed-checker {
  background-color: $bg-color-light;
  padding: 20px 0px;
  margin-bottom: 20px;
  > h2 {
    margin: 0px 0px 10px;
  }
  
}

.seed-checker-table {
  border-collapse: collapse;
  margin: 10px auto;
  background-color: $bg-color;
  
  border-radius: 10px;
  td, th {
    // border: 1px solid #ddd;
    padding: 15px;
    text-align: center;
    
  }

  td.active, th.active {
    background-color: $font-color;
    color: $bg-color;
  }

  th {
    background-color: $bg-color-light;
  }
  
}


@media only screen and (max-width: 600px) {
  .batch-table {
    display: none;
  }
  .nft-item {
    margin: 0 10px 5px;
  }
  .batch-table.-mobile {
    display: flex;
  }
  .header {
    padding:10px;
    h1 {
      font-size: 22px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    p {
      font-size: 12px;
    }
    .logo {
      width: 200px;
    }

   
    
  }


.stat-group {
  span {
    color: $font-color;
    font-size: 10px;
    text-align: center;
    display: block;
  }
  h2 {
    font-size: 12px;
    font-weight: 200;
    text-align: center;
  }
  
}
}
.batch-container {
  margin-bottom: 20px;
  @media only screen and (max-width: 600px) { 
    margin-bottom: 0px;
  }
}

.-full-width {
  width: 100%;

  h2 {
    font-size: 16px;
