      font-weight: bold;
      margin-top: 0;
    }
  }
}
.section-container {
  .nft-item {
    justify-content: space-evenly;
  }

  
}

.section-container.dashboard-values {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  @media only screen and (max-width: 600px) { 
      justify-content: center;

      h2{
        font-size: 20px;
      }
  }
  .nft-item {
    // max-width: 100px;
    // width: 100%;
    width: auto;
    min-width: 100px;
    max-width: initial;
    margin-bottom: 10px;
    background-color: $bg-color-light;
    .stat-group {
      span {
        color: white;
      }
    }
  }
}

.desc {
  color:$font-color;
    text-align: center;
    font-size: 10px;
    margin-bottom: 0px;
    margin-top: 0px;

    &.m-b-20 {
      margin-bottom: 20px;
    }
}


.content.-hero {
  margin-bottom: 100px;
  justify-content: flex-start;
  @media only screen and (max-width: 600px) {
    min-height: 90vh;
    margin-bottom: 0px;
  }

}


.-le-500 {
  max-width: 300px;
}
.le-500 {
  display: flex;
    align-items: center;
    justify-content: center;
}

.donation-list-container {
  max-height: 200px;
  background-color: $bg-color-light;
  max-width: 400px;
  margin: 10px auto;
  overflow-y: scroll;
  border-radius: 5px;
}

.-scroll-bar::-webkit-scrollbar {
  width: 20px;
}

.-scroll-bar::-webkit-scrollbar-track {
  background-color: $bg-color-light;
  border-radius: 100px;
}

.-scroll-bar::-webkit-scrollbar-thumb {
  border-radius: 100px;
  border: 5px solid transparent;
  background-clip: content-box;
  background-color: $bg-color;
}

.donation-list {
  list-style: none;
  padding: 0;
  margin: 0;

  li {
    padding: 10px;
    border-bottom: $bg-color 1px solid;
    font-size: 10px;
    // text-align: center;
  }
}

.plant-grid-item {
    display: flex;
