      }
      margin-right: 20px;
      @media only screen and (max-width: 600px) {
        margin-right: 15px;
      }
    }
    .pgi-r {
      span {
        font: 12px;
      }
      img {
        width: 60px;
      }
      span {
        margin: 0px;
        font-size: 9px;
        border: 1px solid;
        padding: 1px 6px;
        margin-bottom: 5px;
        border-radius: 3px;
        text-align: center;
      }
      p {
        margin: 0px;
        font-size: 9px;
        border: 1px solid $font-color;
        padding: 0px 6px;
        text-align: center;
        // margin-top: 5px;
      }
    }
    
}

.desc-pvu-zone {
